$duration: 6s;
$text-color: pink;
@import url('https://fonts.googleapis.com/css2?family=Passions+Conflict&display=swap');

*,
*::after,
*::before {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

@font-face {
  src: url("https://www.axis-praxis.org/fonts/webfonts/AvenirNext_Variable.woff2") format("woff2");
  font-family: "Avenir";
  font-style: normal;
  font-weight: normal;
}

#main {
  background: url("../img/newYear.jpg") no-repeat center center/cover;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

$wcColor: rgb(0, 0, 0);
$red: red;
$blue: blue;
$green: green;
$hue: 60;
$saturation: 10%;
$lightness: 20%;
$alpha: 1.0;

#main>#welcome {
  font-size: 10rem;
  color: adjust-hue(#000000, $hue);
  position: absolute;
  top: 10vh;
  font-family: 'Passions Conflict', cursive;
  text-shadow: 2px 3px 3px $green,
    2px 3px 4px $blue,
    2px 3px 5px $green;
}

#main>#inputContainer {
  height: 30vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

#main>#inputContainer>#nameLabel {
  font-variation-settings: "wght" 400, "wdth" 100;
  font-size: 5em;
  text-align: center;
  position: absolute;
  animation: text-flow $duration infinite;
  color: $text-color;
  text-shadow: 2px 3px 4px red,
    2px 4px 5px black,
    2px 5px 6px red;
  font-family: "Avenir";
}

@keyframes text-flow {
  25% {
    font-variation-settings: "wght" 900, "wdth" 100;
    filter: hue-rotate(0deg);
  }

  50% {
    transform: translateY(-100px);
    font-variation-settings: "wght" 400, "wdth" 100;
  }

  75% {
    transform: translateY(0%);
    font-variation-settings: "wght" 900, "wdth" 100;
    filter: hue-rotate(90deg);
  }
}

@media only screen and (max-width:600px) {

  #main>#welcome {
    font-size: 3.5rem;
    top: 10vh;
  }

  #main>#inputContainer>#nameLabel {
    font-variation-settings: "wght" 400, "wdth" 100;
    font-size: 3em;
    text-align: center;
    position: absolute;
    animation: text-flow $duration infinite;
    color: $text-color;
    text-shadow: 2px 3px 4px red,
      2px 4px 5px black,
      2px 5px 6px red;
    font-family: "Avenir";
  }
}